<template>
  <div>
    <SumContainer>
      <SumItem label="Btc" :number="sumBtc| numFormat('0,0.000')"/>
      <SumItem label="Eth" :number="sumEth| numFormat('0,0.00')"/>
      <SumItem label="Usdt" :number="sumUsdt| numFormat('0,0')"/>
      <SumItem :label="'Total $ ('+ list.length +' weeks)'" :number="total| numFormat('0,0')" v-if="list"/>
      <SumItem label="Total $ (if sell now)" :number="(sumEth*eth.current_price+btc.current_price*sumBtc)| numFormat('$ 0,0')" ex-class="grey"/>
    </SumContainer>
    <div class="fc mt20">
      <trend
        :data="list?list.map(item=>item.total):[]"
        :gradient="['green', 'white']"
        :autoDrawDuration="300"
        :stroke-width="2"
        smooth
        :width="700"
        :height="200"
        class="card"
      ></trend>
    </div>
    <FundForm v-if="$route.name!=='ak'"/>

    <ul class="card p20">
      <FundListItem v-for="item in list" :key="item.id" :item="item"/>
    </ul>
  </div>
</template>

<script>
  import FundForm from './FundForm';
  import SumItem from '../sum/SumItem';
  import SumContainer from '../sum/SumContainer';
  import FundListItem from './FundListItem';
  export default {
    props:['list'],
    components: {
      FundListItem,
      SumItem,
      SumContainer,
      FundForm,
    },

    computed: {
      sumBtc(){
        if(this.list){
          return this.list.map(item=>item.sumBtc).reduce((a, b) => a+b);
        }else return 0
      },
      sumEth(){
        if(this.list){
          return this.list.map(item=>item.sumEth).reduce((a, b) => a+b);
        }else return 0
      },
      sumUsdt(){
        if(this.list){
          return this.list.map(item=>item.sumUsdt).reduce((a, b) => a+b);
        }else return 0
      },
      total(){
        if(this.list){
          return this.list.map(item=>item.total).reduce((a, b) => a+b);
        }else return 0
      },
      btc(){
        if(this.$store.state.myGeckoList.length!==0) {
          return this.$store.state.myGeckoList.find(item=>item.symbol === 'btc');
        }else return 0
      },
      eth(){
        if(this.$store.state.myGeckoList.length!==0) {
          return this.$store.state.myGeckoList.find(item=>item.symbol === 'eth');
        }else return 0
      },
    },
  };
</script>

