<template>
  <li class="item pr">
    <div class="block1">
      <span class="numberBoxExtra grey mr10 hover" @click="dropdown(item.id)">{{item.date}}</span>
      <div class="groupOf3">
        <span class="grey label">btc: </span>
        {{item.sumBtc | numFormat('0,0.00000')}}
        <span class="grey">({{item.btcPercentage | numFormat('0,0.00')}}%) </span>
      </div>
      <div class="groupOf3">
        <span class="grey label">eth: </span>
        {{item.sumEth | numFormat('0,0.00')}}
        <span class="grey">({{item.ethPercentage | numFormat('0,0.00')}}%) </span>
      </div>
      <div class="groupOf3">
        <span class="grey label">usdt: </span>
        {{item.sumUsdt | numFormat('0,0')}}
        <span class="grey">({{item.usdtPercentage | numFormat('0,0.00')}}%) </span>
      </div>
      <div class="groupOf2">
        <span class="grey label">btcPrice: </span>
        <span class="numberBoxExtra">{{item.btcRate| numFormat('0,0')}} </span>
      </div>
      <div class="groupOf2">
        <span class="grey label">ethPrice: </span>
        <span class="numberBox">{{item.ethRate| numFormat('0,0')}} </span>
      </div>
      <div class="groupOf2">
        <span class="grey label">total: </span>
        <span class="numberBox">{{item.total| numFormat('0,0')}} </span>
      </div>
      <div class="groupOf2">
        <span class="grey label">gone up1: </span>
        <span class="numberBox">{{goneUp(item)| numFormat('0,0')}} </span>
      </div>
    </div>

    <button @click="deleteItem(item.id)" class="delBox" v-if="$store.state.userId[0]==='4'">x</button>
    <Transition name="slide-fade">
      <FundForm
        :item="item"
        v-if="isDropped"
        inside="true"
      />
    </Transition>
  </li>
</template>

<script>
  import FundForm from './FundForm';
  import SumItem from '../sum/SumItem';
  import SumContainer from '../sum/SumContainer';
  export default {
    props:['item'],
    components: {
      SumItem,
      SumContainer,
      FundForm,
    },

    data() {
      return {
          openedItemId:null,
          isDropped:false,
      };
    },

    methods: {
      goneUp(item){//в методах а не компутед пот что при кажд итерации происх рендер = много действий
        const lvl1 = 0.2;
        const lvl2 = 0.08;
        const lvl3 = 0.04;
        const aBtc = item.a&&item.a.btc?item.a.btc:0
        const kBtc = item.k&&item.k.btc?item.k.btc:0
        const pBtc = item.p&&item.p.btc?item.p.btc:0
        const aEth = item.a&&item.a.eth?item.a.eth:0
        const kEth = item.k&&item.k.eth?item.k.eth:0
        const pEth = item.p&&item.p.eth?item.p.eth:0
        const aUsdt = item.a&&item.a.usdt?item.a.usdt:0
        const kUsdt = item.k&&item.k.usdt?item.k.usdt:0
        const pUsdt = item.p&&item.p.usdt?item.p.usdt:0

        return aBtc*lvl1*item.btcRate+
          kBtc*lvl2*item.btcRate+
          pBtc*lvl3*item.btcRate+
          aEth*lvl1*item.ethRate+
          kEth*lvl2*item.ethRate+
          pEth*lvl3*item.ethRate+
          aUsdt*lvl1+
          kUsdt*lvl2+
          pUsdt*lvl3
      },
      dropdown(id){
        this.openedItemId = id;
        this.isDropped = !this.isDropped;
      },
      deleteItem(id){
        this.$store.commit('deleteItem', {id,listName:'fundList'});
      },
    }
  };
</script>

